html {
    background-color: #17171d;
}

iframe {
    width: calc(100% - 40px);
    height: 321px;
    position: absolute;
    bottom: 0;
    left: 0;
    border: 0;
    margin: 20px
}

.bottom-button {
    margin-top: 12px !important;
}

.all-form-inputs {
    min-width: 180px !important;
    margin-left: 30px !important;
    margin-right: 30px !important;
    margin-bottom: 12px !important;
}


#full-screen {
    position: absolute;
    width: calc(100% - 40px);
    bottom: 340px;
    left: 0;
    border: 0;
    margin: 20px;
    color: rgb(186, 191, 196)
}

.s-container {
    border-radius: 15px;
    border: 1px solid #31366d;
    width: fit-content;
    display: inline-block;
    margin: 12px;
    padding: 15px 15px 3px;
}

.music-text {
    margin-right: 20px;
    margin-left: 20px;
    position: relative;
    top: 6px;
}


@media (max-height: 900px) and (min-width: 1111px) {

    iframe {
        position: relative;
        bottom: unset;
        margin: 20px;
    }


    #full-screen {
        position: relative;
        bottom: unset;
        margin-top: 20px;
        margin-bottom: 0;
    }

}

@media (max-height: 1200px) and (max-width: 1111px) {

    iframe {
        position: relative;
        bottom: unset;
        margin: 20px;
    }


    #full-screen {
        position: relative;
        bottom: unset;
        margin-top: 20px;
        margin-bottom: 0;
    }

}

@media (max-width: 575px) {

    iframe {
        position: relative;
        bottom: unset;
        margin: 20px;
    }


    #full-screen {
        position: relative;
        bottom: unset;
        margin-top: 20px;
        margin-bottom: 0;
    }

    .bottom-button {
        width: 100%;
    }

    label {
        width: 100%;
    }

}